import React, {useEffect, useState} from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min';
import {useNavigate} from 'react-router-dom';
import {Field, Formik, Form, ErrorMessage} from "formik";
import { addNewStudent } from "../service/studentService";
import * as Yup from 'yup';
import '../css/AddComponent.css' ;
import { getAllLocation } from "../service/locationService";
import {toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
function AddComponentForm(){
    const [locationList, setLocationList] = useState([]);
    const [student, setStudent] = useState({
        // id:"", 
        name:"", 
        phone:"", 
        email:'', 
        gender: 'Nam', 
        interest: ['Nghe nhc'], 
        location: '' 
    })
    useEffect(()=>{
        const fetchData = async()=>{
            const list = await getAllLocation();
            setLocationList(list);
        }
        fetchData();
    })
    const navigate = useNavigate();
    const handleSubmit= async(value)=>{
        console.log(value);
        const student ={
            ...value,
            location: JSON.parse(value.location)
        }
        await addNewStudent(student);
        toast.success("Thm mi thnh cng");
        navigate('/student');
    }
    const handleValidate = Yup.object({
        // id:Yup.number().required("ID khng c  trng")
        // .min(1, 'Phi l s nguyn dng'),
        name: Yup.string().required('Yu cu k  trng')
        .matches(/^[A-Z][a-z]*(\s[A-Z][a-z]*)+$/,'Tn cha ng nh dng'),
        phone: Yup.number().required('Yu cu k  trng'),
        email: Yup.string().required('Yu cu k  trng')
        .matches(/^\S+@\S+\.\S+$/,'Email cha ng nh dng'),
        location: Yup.string().required('Yu cu chn gi tr'),
        interest: Yup.array().min(1, 'Yu cu chn t nht mt gi tr'),

    })
    //validate: Kim tra tnh hp l ca d liu trc khi thm mi

    return(
        <>
            <Formik initialValues={student} onSubmit={handleSubmit} validationSchema={handleValidate}>
                <Form>
                    {/* <div>
                        <label>ID:</label>
                        <Field type="text" name='id'/>
                        <ErrorMessage style={{color:'red'}} name="id" component='div'/>
                    </div> */}
                    <div>
                        <label>Name:</label>
                        <Field type="text" name='name'/>
                        <ErrorMessage style={{color:'red'}} name="name" component='div'/>
                    </div>
                    <div>
                        <label>Phone:</label>
                        <Field type="text" name='phone'/>
                        <ErrorMessage style={{color:'red'}} name="phone" component='div'/>
                    </div>
                    <div>
                        <label>Email:</label>
                        <Field type="text" name='email'/>
                        <ErrorMessage style={{color:'red'}} name="email" component='div'/>
                    </div>
                    <div>
                        <label>Gender:</label>
                        <Field type='radio' name='gender' value='Nam'/> Nam
                        <Field type='radio' name='gender' value='N'/> N
                        <ErrorMessage style={{color:'red'}} name="gender" component='div'/>
                    </div>
                    <div>
                        <label>Interest:</label>
                        <Field type='checkbox' name='interest' value='Nghe nhc'/> Nghe nhc 
                        <Field type='checkbox' name='interest' value='c sch'/> c sch
                        <Field type='checkbox' name='interest' value='Th thao'/> Th thao
                        <ErrorMessage style={{color:'red'}} name="interest" component='div'/>
                    </div>
                    
                    <div>
                        <label>Location:</label>
                        <Field as='select' name='location'>
                            <option>----Select----</option>
                            {locationList.map((m)=>(
                                <option value={JSON.stringify(m)}>{m.name}</option>
                            ))}
                            
                            
                        </Field> 
                        <ErrorMessage style={{color:'red'}} name="location" component='div'/>
                    </div>

                    <div>
                        <button className="btn btn-sm btn-success" type="submit">Save</button>
                    </div>
                    
                </Form>
            </Formik>
        </>
    )
}
export default AddComponentForm;